<template>
	<div id="main">
    <div class="sortMenu clearfix">
      <mt-navbar class="sortMenu-ul" v-model="selected">
        <mt-tab-item v-bind:id="index" v-bind:key="index" @click.native="getInfos(index)" v-for="(category,index) in categoryList">
          {{category.title}}
        </mt-tab-item>
      </mt-navbar>
    </div>
    <div>
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
      <mt-tab-container v-model="selected">
        <mt-tab-container-item  id="1" style="display:block;">
          <mt-cell v-bind:key="index"   v-for="(info,index) in infoList" :title="info.title" @click.native="gotoDetail(index)" >
            <span><i class="right"></i></span>
          </mt-cell>
        </mt-tab-container-item>
      </mt-tab-container>
    </mt-loadmore>
  </div>
  <div>
    <div class="help">
      <span>没有找到想问的问题？</span>
      <a href="tel:95399">
        <img class="img-responsive service"  src="../assets/images/helpService.png">
        <span>问问在线客服</span> 
      </a>
      <a @click="gotoService">
        <img class="img-responsive phone" src="../assets/images/helpPhone.png">
        <span>拨打客服电话</span> 
      </a>
    </div>
  </div>

</div>
</template>

<script>
import config from "../config.js"
import { Indicator, Loadmore } from 'mint-ui';
export default {
  name: 'Main',
  data () {
    return {
      search:null,
      selected:0,
      categoryList:null,
      infoList:null,
      categoryIndex:0,
      allLoaded:false,
      msg: 'Welcome to Your Vue.js App',
      infoList:[{
        title:"绑定银行卡失败，怎么办？ 绑定银行卡失败，怎么办？ 绑定银行卡失败，怎么办？ 绑定银行卡失败，怎么办？"
      },{
        title:"绑定银行卡失败，怎么办？"
      },{
        title:"绑定银行卡失败，怎么办？"
      }]
    }
  },
  created:function(){  
    let titleObject={
      back:true,
      title:"帮助中心",
      search:true
    };
    this.$emit("routerChange",titleObject);
    this.initCategorys();
    let categoryIndex =this.$route.params.index; 
    if(categoryIndex!=null&&categoryIndex!=undefined){
      this.selected= categoryIndex;
    }
  },
  methods:{
    initCategorys:function(){
      console.log("initCategorys");
      let url = config.getCategorys;
      let that = this;
      Indicator.open();
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.categoryList=data.result;
          that.getInfos(that.selected);
        }
      });
    },
    gotoSearch:function(){
      this.$router.push({name:"search"});
    },
    getInfos:function(index){
      Indicator.open();
      let utilUrl = config.getInfosByCategoryId;
      let category = this.categoryList[index];
      let url = utilUrl+"?"+"id="+category.id+"&pageNumber=0"+"&pageSize=6";;
      let that = this;
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.infoList=data.result;
        }
      });
    },
    gotoDetail:function(index){
      console.log(index);
      let info =this.infoList[index];
      console.log(info);
      this.$router.push({name:"content",params:{info:JSON.stringify(info)}});
    },
    imageLoad:function($event){
      if($event.target){
        $event.target.parentNode.parentNode.firstChild.style.height=$event.target.height+"px";
      }
    },
    gotoService:function(){
      var url ="http://www17.53kf.com/m.php?cid=70867336&arg=9007838&style=1";
      window.location.href = url + "?rnd=" + new Date().getTime();
    },
    loadBottom:function(){
      console.log("loadBottom");
    }
  }
}
</script>

<style>
#main{
  background: #fff;
  height: 100%;
}
#main .mintui.mintui-search{
  font-size: 20px;
}
#main .nav{
  display: flex;
  flex-flow: nowrap;
}
#main .mint-cell{
  margin-left: 20px;
  border-bottom: 0.5px solid #dcdcdc;
}
#main .mint-cell-wrapper {
  padding-left: 0;
  padding-right: 30px;
}
.mint-cell i:after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 51%;
  right: 20px;
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}

#main .title{
  margin-top: 10px;
  margin-bottom: 10px;
}
#main .help-nav{
  margin-top: 20px;
  margin-bottom: 20px;
}

#main .title .title-lead-wrap{
  padding-top: 5px;
  padding-bottom: 5px;
}
#main .title .title-lead{
  color: #000000;
  font-size: 1.6rem;
}
#main .allSortWrap.col-xs-12{
  padding-left: 0;
  padding-right: 0;
}
.searchbar-inner{
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-color: #F5F5F5;
  border-radius: 15px;
  padding: 5px 10px;
}
.searchbar-inner .mintui-search{
  font-size: 1.4rem;
  color: #d9d9d9;
}
.searchbar-inner .mintui-search-text{
  width: 80%;
  font-size: 1.2rem;
  color: #515151;
  padding-top: 2px;
  text-align: center;
}
.allSort{

}

.sortMenu{
  width: 100%; 
  background-color:#fff; 
  overflow-x: scroll; 
  -webkit-overflow-x: scroll;
  border-bottom: 0.5px solid #dcdcdc;
}
.sortMenu::-webkit-scrollbar{ 
  width: 0; 
  height: 0;   
  background-color: #fff;  
}
.sortMenu-ul { 
  min-width:300px; 
  display: flex;
  justify-content: flex-start;
  text-align: center;
  font-size: 13px;
  word-break: keep-all;
  padding-top: 13px;
  padding-bottom: 12px;
  color:#000000;
}
.sortMenu .cell{ 
  display: inline-block; 
  font-size: 1.4rem;
  margin: 0px 0.6rem;
  height: 22px;
  line-height:22px;

}
.sortMenu .cell.special a{
  color: #ff474c;
}
.sortMenu .cell.special:before {
  content: '';
  height: 2px;
  width: 100%;
  background: #ff474c;
  position: absolute;
  bottom: 0px;
}
.sortMenu .all{
  right: 0;
  top: 0;
  height: 30px;
  width: 30px;
  position: absolute;
  background: #fff;
  z-index: 10;
  display: flex;
  justify-content:center;
  align-items:center;
}
.sortMenu .all:before{
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  box-shadow: 1px 0px 1px #e0e0e0;
  left: 0px;
}
#main a:hover, a:focus{
  text-decoration: none;
}
#main .allSort, a{
  color: #000;
}
#main  a.is-selected{
  color: red;
  border: 0.5px solid;
  border-radius: 100px;
  padding: 8px 15px;
  margin-bottom: 0;
}
#main .mint-tab-item{
  padding: 8px 15px;
}
#main .mint-navbar > a:first-child{
  margin-left: 10px;
}
#main .mint-navbar > a:last-child{
  margin-right: 10px;
}
#main .mint-cell-wrapper{
  font-size:14px;
  color:#000000;
}
#main .mint-cell-wrapper .mint-cell-title{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#main .mint-navbar .mint-tab-item {
  margin-right: 5px;
}

#main .mint-tab-item-label{
  padding-top: 1px;
  padding-bottom: 1px;
}
#main .help{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  font-size: 11px;
  padding: 17px 10px 17px 10px;
  text-align: center;
  border-top: 0.5px solid #dcdcdc;
}
#main .help a{
  display: flex;
}
#main .help .phone{
  width:12px;
  height:15px;
  margin-right: 5px;
  margin-left: 10px;
}
#main .help .service{
  width:17px;
  height:16px;
  margin-right: 5px;
  margin-left: 10px;
}
</style>


