<template>
	<div id="search">
    <div>
      <mt-search v-model="search" cancel-text="取消" placeholder="搜一搜" :result.sync="result">
        <mt-cell v-bind:key="item.id" v-for="(item,index) in result" @click.native="gotoDetail(index)" :title="item.title" >
            <span v-if="item.id!=-1"><i class="right"></i></span>
        </mt-cell>
      </mt-search>
    </div>
  </div>
</template>

<script>
import config from "../config.js"
import util from "../utils/commonUtils"
export default {
  name: 'Content',
  data () {
    return {
      search:null,
      result:[],
      noResult:[{
        title:"没有搜索到相应问题，请您更换关键字！",
        id:-1
      }],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  watch:{
    search:function(newVal,oldVal){
      console.log("newVal:"+newVal);
      console.log("oldVal:"+oldVal);
      let val=util.removeAllSpace(newVal);
      if(val==""||val==null||val==" "){
        return;
      }
      this.searchInfos(val);
    }
  },
  created:function(){  
    let titleObject={
      back:true,
      title:"客户服务搜索",
      search:false
    }
    this.$emit("routerChange",titleObject);
  },
  methods:{ 
    searchInfos:function(key){
      let url = config.search;
      let param={
        key:key
      }
      let that = this;
      this.$http.post(url,param).then(function(response){
        let data=response.body;
        if(data.resCode==0){
          if(data.result.length>0){
            that.result=data.result;
          }else{
            that.result=that.noResult;
          }
        }
      });
    },
    gotoDetail:function(index){
      console.log(index);
      let info =this.result[index];
      if(info.id==-1){
        return;
      }
      this.$router.push({name:"content",params:{info:JSON.stringify(info)}});
   }
 }
}
</script>

<style>
#search{
  font-size:14px;
  background: #fff;
}
#search .mint-search-list{
  padding-top: 98px;
}
#search .mint-searchbar-core{
  margin-left: 3px;
}
#search .mint-cell-wrapper{
  font-size:14px;
  padding: 0 16px;
}
#search .mint-searchbar{
  background:#c4011d;
}

#search .mint-searchbar-inner{
  background: #ffffff;
  border-radius: 100px;
  height: 30px;
}
#search .mint-searchbar .mint-searchbar-cancel{
  font-size: 16px;
  color: #FFFFFF;
}
#search .mint-cell i:after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 51%;
  right: 20px;
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}

</style>


