<template>
  <div id="app">
    <div class="main-nav" v-show="titleObject.back">
      <div>
        <mt-header  :title="titleObject.title">
          <div @click="goBack" slot="left">
            <mt-button icon="back"></mt-button>
          </div>
          <mt-button v-show="titleObject.search" @click.native="gotoSearch" icon="search" slot="right"></mt-button>
        </mt-header>
      </div>
    </div>
    <router-view v-on:routerChange="routerChange"></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      titleObject:{
        back:false,
        title:""
      }
    }
  },
  created:function(){

  },
  methods:{
    goBack:function(){
      this.$router.go(-1);
      //this.$router.push({ name: 'main' });
    },
    routerChange:function(data){
      this.titleObject=data;
    },
    gotoSearch:function(){
      this.$router.push({ name: 'search' });
    }
  }
}
</script>

<style>
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

html {
  height: 100%;
}

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  line-height: 1.4;
  width: 100%;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  background: #F1F1F1;
}
#app{
  height: 100%;
}

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, textarea, p, blockquote, th, td, figure {
  padding: 0;
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img {
  border: 0;
  display: block;
}

a {
  text-decoration: none;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-weight: normal;
  font-style: normal;
}

ol, ul {
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}

.clearfix:after {
  content: '\20';
  clear: both;
  height: 0;
  display: block;
}
.full {
  width: 100%;
}
.hide {
  display: none;
}
.clearfix {
  *zoom: 1;
}
.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

body img {
  height: auto;
}

footer {
  margin: 0 auto;
}
footer p {
  font-size: 13px;
  color: #762f2d;
  text-align: center;
}
.red{
  color: red;
}
.visibility{
  visibility: hidden
}

#app .mint-msgbox{
  border-radius: 10px;
}
#app .mint-msgbox .mint-msgbox-btn{
  font-size: 14px;
}
#app .mint-header{
  background:#c4011d;
  font-size:16px;
  height: 44px;
  color:#ffffff;
}

</style>



