
function getViewSize() {
	return {
		"w": window['innerWidth'] || document.body.clientWidth,
		"h": window['innerHeight'] || document.body.clientHeight
	}
}

function getPixcs(){
	return {
		w:screen.width,
		h:screen.height
	}
}

function getFullSize(){
	var widch = Math.max(document.documentElement.clientWidth, document.body.clientWidth) + Math.max(document.documentElement.scrollLeft, document.body.scrollLeft);
	var height = Math.max(document.documentElement.clientHeight, document.body.clientHeight) + Math.max(document.documentElement.scrollTop, document.body.scrollTop);
	widch = Math.max(document.documentElement.scrollWidth, widch);
	height = Math.max(document.documentElement.scrollHeight, height);
	return {
		"widch": widch,
		"height": height
	};
}

function isEmptyObject(e) {  
	var t;  
	for (t in e)  
		return !1;  
	return !0  
}

function removeAllSpace(str) {
	return str.replace(/\s+/g, "");
}

export default{
	getViewSize,
	getFullSize,
	getPixcs,
	isEmptyObject,
	removeAllSpace
}


